import Header from "../components/layout/Header";
import Footer from "../components/layout/Footer";
import PageHeader from "../components/shared/PageHeader";
import ShowroomCard from "../components/shared/ShowroomCard";
import { Building2, MapPin, Search } from "lucide-react";
import { SHOWROOMS, CITIES } from "@/lib/data";
import type { Metadata } from "next";

export const metadata: Metadata = {
    title: "Concessionnaires Automobiles en Alg√©rie | DzairAuto",
    description: "Trouvez les concessionnaires et showrooms automobiles pr√®s de chez vous.",
};

export default function ConcessionnairesPage() {
    return (
        <div className="min-h-screen bg-gray-50">
            <Header />

            <PageHeader
                icon={Building2}
                badge="R√©seau national"
                title="CONCESSIONNAIRES"
                highlight="AUTOMOBILES"
                description={`${SHOWROOMS.length}+ showrooms et concessionnaires √† travers l'Alg√©rie`}
            />

            {/* Search & Filter */}
            <section className="py-6 bg-white shadow-sm">
                <div className="max-w-7xl mx-auto px-4">
                    <div className="flex flex-col md:flex-row gap-4">
                        <div className="flex-1 relative">
                            <input
                                type="text"
                                placeholder="Rechercher un concessionnaire..."
                                className="w-full pl-10 pr-4 py-3 bg-gray-50 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#D32F2F]/20"
                            />
                            <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
                        </div>
                        <select className="px-4 py-3 bg-gray-50 border border-gray-200 rounded-lg md:w-48 focus:outline-none focus:ring-2 focus:ring-[#D32F2F]/20">
                            <option>Toutes les villes</option>
                            {CITIES.map((city) => (
                                <option key={city}>{city}</option>
                            ))}
                        </select>
                        <button className="px-6 py-3 bg-[#D32F2F] text-white rounded-lg font-semibold hover:bg-[#B71C1C]">
                            Rechercher
                        </button>
                    </div>
                </div>
            </section>

            {/* Cities Quick Filter */}
            <section className="py-4 border-b border-gray-200 bg-white">
                <div className="max-w-7xl mx-auto px-4">
                    <div className="flex flex-wrap gap-2">
                        <button className="px-4 py-2 bg-[#D32F2F] text-white rounded-full text-sm font-medium">
                            Toutes
                        </button>
                        {CITIES.slice(0, 8).map((city) => (
                            <button
                                key={city}
                                className="px-4 py-2 bg-gray-100 text-gray-700 rounded-full text-sm font-medium hover:bg-gray-200"
                            >
                                {city}
                            </button>
                        ))}
                    </div>
                </div>
            </section>

            {/* Showrooms Grid */}
            <section className="py-10">
                <div className="max-w-7xl mx-auto px-4">
                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                        {SHOWROOMS.map((showroom) => (
                            <ShowroomCard key={showroom.id} showroom={showroom} />
                        ))}
                    </div>

                    {/* Load More */}
                    <div className="text-center mt-10">
                        <button className="px-10 py-4 bg-gray-100 text-gray-700 rounded-full font-semibold hover:bg-gray-200">
                            Charger plus de concessionnaires
                        </button>
                    </div>
                </div>
            </section>

            {/* Map Placeholder */}
            <section className="py-10 bg-white">
                <div className="max-w-7xl mx-auto px-4">
                    <h2 className="text-2xl font-bold text-gray-900 mb-6 text-center">
                        Trouvez un concessionnaire pr√®s de chez vous
                    </h2>
                    <div className="h-80 bg-gray-200 rounded-2xl flex items-center justify-center">
                        <div className="text-center">
                            <MapPin className="w-12 h-12 text-gray-400 mx-auto mb-3" />
                            <p className="text-gray-500">Carte interactive</p>
                            <p className="text-gray-400 text-sm">Bient√¥t disponible</p>
                        </div>
                    </div>
                </div>
            </section>

            <Footer />
        </div>
    );
}
